import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        isLodaing:false,
    },

    actions:{
        updatedloading(context,status) {
             context.commit('LOADING',status);
        },
    },

    mutations:{
        LOADING(state,status){
            state.isLodaing=status;
        }
    }


});


